import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { BooksOfAccountService } from 'src/app/core/services/books-of-account.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-journal',
  templateUrl: './journal.component.html',
  styleUrls: ['./journal.component.scss']
})
export class JournalComponent implements OnInit {
  public JournalData:any=[];
  public JournalDetails:any=[];
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))

  constructor(private BooksOfAccountService:BooksOfAccountService,private toastService: ToastNotificationService, private titleCasePipe: TitleCasePipe) { }

  ngOnInit(): void {
    this.getJournalData();
  }
  getJournalData(){
    this.BooksOfAccountService.getJournalList({},this.currentUser.id).then((res:any)=>{
      console.log("RES JOURNAL DATA",res)
      this.JournalData=res.data.transactions;
      this.JournalDetails=res.data

    },(err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
        // this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
        // this.toastService.openErrorSnackBar("Something Went Wrong.");
      }
    }
    )

  }


}
