import { Injectable } from '@angular/core';
import { UrlConstants } from 'src/app/_helpers/url-constants';
import { HttpService } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class BooksOfAccountService {

  constructor(public httpClient: HttpService) { }
  getJournalList(data:{},userId){
    return new Promise((resolve, reject) => {
      this.httpClient.inventory(data, UrlConstants.getJournalList +userId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });

  }
  getTrialList(data:{},userId){
    return new Promise((resolve, reject) => {
      this.httpClient.inventory(data, UrlConstants.getTrialList +userId , 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });

  }
  
}
