import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { PreviewComponent } from '../../ledger/preview/preview.component';
import { BooksOfAccountService } from 'src/app/core/services/books-of-account.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { TitleCasePipe } from '@angular/common';

@Component({
  selector: 'app-trial-balance',
  templateUrl: './trial-balance.component.html',
  styleUrls: ['./trial-balance.component.scss']
})
export class TrialBalanceComponent implements OnInit {
public currentUser = JSON.parse(localStorage.getItem("currentUser"))
public trialBalanceData : any = []
public grandTotalClosingBalance:any;
public grandTotalOpeningBalance:any;

  constructor(public dialog: MatDialog,private BooksOfAccountService:BooksOfAccountService,private toastService: ToastNotificationService, private titleCasePipe: TitleCasePipe) { }
 
  ngOnInit(): void {
    this.getTriaData();
  }
  getTriaData(){
    this.BooksOfAccountService.getTrialList({},this.currentUser.id).then((res:any)=>{
      console.log("RES TRIAL DATA",res)
      this.trialBalanceData=res.data.data
      this.grandTotalOpeningBalance=res.data.grandTotalOpeningBalance
      this.grandTotalClosingBalance=res.data.grandTotalClosingBalance

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
    }
    )

  }

  openPreviewDialog(): void {
    const dialogRef: MatDialogRef<PreviewComponent> = this.dialog.open(PreviewComponent, {
      width: '700px', // Adjust the width as per your requirement
      height : '100vh',
      data: { },
      panelClass: 'custom-dialog-class',
      disableClose:true,
      position: {
        right: '0',
      },
    });

    dialogRef.afterClosed().subscribe((result) => {
      console.log(`Dialog result: ${result}`);
    });
  }
}
